.main-aboutus-page {
  background-color: #150f22;
}
.statsec{
    padding-top: 100px;
    padding-bottom: 60px;
}
.main-gradient {
  background-image: url("../../../../../assets/images/internalpages-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}
.main-heading-div {
  h4 {
    color: #fff;
    font-family: Urbanist;
    font-size: 12px;
    font-weight: 400;

    @media (max-width: 768px) {
      text-align: center;
    }
  }

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 40px;
    font-weight: 500;

    @media (max-width: 1024px) {
      font-size: 32px;
    }

    @media (max-width: 768px) {
      font-size: 28px;
      text-align: center;
    }

    @media (max-width: 480px) {
      font-size: 24px;
    }
  }
}
.heading-div {
  padding: 93px 0px;

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 48px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  p {
    color: #fff;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
}

.stats-row {
  border-radius: 12px;
  background: #6450b1;
  text-align: center;
  color: #fff;
  padding:30px 25px;
  box-shadow: 0px 0px 50px rgba(255, 255, 255, 0.3);
}
.stats-box h3 {
 color: #FFF;
text-align: center;
font-family: Urbanist;
font-size: 38px;
font-style: normal;
font-weight: 500;
line-height: normal;
}

.stats-box p {
  color: rgba(255, 255, 255, 0.66);
text-align: center;
font-family: Urbanist;
font-size: 22px;
font-style: normal;
font-weight: 400;
line-height: normal;
margin-bottom: 0;
}
.divider-col {
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}

/* Remove divider lines on small screens */
@media (max-width: 767px) {
    .divider-col {
      border-top: 1px solid rgba(255, 255, 255, 0.5);
      border-bottom: 1px solid rgba(255, 255, 255, 0.5);
      border-left:none;
      border-right: none;
        margin: 20px 0; /* Add some vertical spacing */
        padding: 20px 0;
  }
}



.about-section {
  padding-top: 60px;
  padding-bottom: 120px;
}

.section-title {
  color: #FFF;
font-family: Urbanist;
font-size: 32px;
font-style: normal;
font-weight: 600;
line-height: normal;
display: flex;
gap: 10px;
align-items: center;
white-space: nowrap;
margin-bottom: 20px;
span{
    width: 100%;
    height: 4px;
    background:  linear-gradient(130deg, #492FB2 -1.74%, rgba(73, 47, 178, 0.61) 125.81%);
}
  @media (max-width: 576px) {
      span{
        display: none;
      }
    }
}

.section-desc {
color: #FFF;
font-family: Urbanist;
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: 180%; /* 32.4px */
}

.section-list {
  list-style: none;
  padding-left: 0;
}

.section-list li {
  position: relative;
  margin-left: 1rem; 
  padding-left: 1rem;
color: #FFF;
font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: 220%;
}

.section-list li::before {
  content: "â€¢";
  position: absolute;
  left: 0;
  top: 0;
  color: #fff;
  font-size: 2rem;
}
